* {
  color: inherit;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

h1 {
  display: block;
  font-family: monospace;
  font-size: 3.5em;
  margin-top: 0px;
  margin-bottom: 10px;
  width: 230px;
  text-align: center;
}

li {
  list-style: none;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

body {
  background-image: url("https://i.pinimg.com/originals/df/80/8f/df808f6bfc95b04ecfa02502c905ca7e.jpg");
  background-size: cover;
  margin: 0 !important;
}

.banner {
  display: flex;
  height: 80px;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 60px;
}


.links {
  width: 190px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  margin-top: 5px;

  img {
    width: 50px;
    height: 50px;
    cursor: pointer;
    

  }
}

.modal-container {
  z-index: 5;
  position:fixed;
  padding:0;
  margin:0;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background:rgba(255,255,255,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal {
  padding: 0 5px;
  border: 2px solid grey;
  background-color: rgba($color: #ffffff, $alpha: 1.0);
  border-radius: 5px;
  z-index: 5;
  height: 400px;
  width: 400px;
}

.hidden {
  display: none;
}

.content {
  display: flex;
  background-image: url('https://www.lavalamp.com/wp-content/uploads/2016/08/SILVER-BACKGROUND.jpg');
  background-size: cover;
  max-width: 1200px;
  max-height: 400px;
  width: 96vw;
  border: 5px solid rgb(0, 0, 0);
  outline: 5px solid rgb(112, 0, 0);
  border-radius: 2px;
  margin: 0 auto;
  box-shadow: 3px 100px 70px 30px rgba(0,0,0,0.8);
}


@media (max-width: 768px) {
  .content {
    width: 85%;

    max-width: 500px;
  }
}
.settings {
  height: 408px;
  width: 200px;
  text-align: center;
  background-color: rgba($color: #000000, $alpha: 0.5);
  border-right: 2px solid black;
  


  .waveform {
    margin: 0 auto 20px;
    width: 120px;
    height: 120px;
    display: flex;
    flex-wrap: wrap;
  }

  .waveform-img {
    background-color: black;
    display: block;
    color:  rgb(94, 94, 94);
    cursor: pointer;
    border: 7px solid black;
    height: 45px;
    width: 45px;
    margin: 0 0.5px;
  }
  .selected {
    border-color: rgb(170, 170, 170) !important;
  }
}

@media (max-width: 768px) {
  .settings {
    width: 100%;

  }
}

.sliders {
  display: flex;
  height: 100px;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 0;
}


.slider-labels {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
}


.slider-inputs {
  height: 96%;
  box-sizing: border-box;
  padding: 4px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;


  input {
    -webkit-appearance: none; 
    -moz-appearance: none; 
         appearance: none;
    background: #000000; 
    outline: none; 
    height: 2px;
    width: 100px;
  }

  input::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 1px solid #000000;
    height: 18px;
    width: 10px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
    box-shadow: 3px -2px 5px 1px rgba($color: #000000, $alpha: 0.5); 
  }
}

.keyboard {
  height: 408px;
  flex: 1;
}

@media (max-width: 768px) {
  .keyboard {
    display: none;
  }
}

div[class^="row-"] {
  height: 100px;
  display: flex;
}

.row-1 {
  padding-left: 5px;
  padding-right: 50px;

}

.row-2 {
  padding-left: 25px;
  padding-right: 25px;
}

.row-3 {
  padding-left: 25px;
  padding-right: 50px;
}

.row-4 {
  padding-left: 50px;
  padding-right: 5px;
}

button[class^="key"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  display: block;
  text-decoration: none;
  height: 75px;
  border: 2px solid #404040;
  flex: 1;
  margin: 10px 0.2em;
  color: rgba($color:  #aaaaaa, $alpha: 1);
  font-size: larger;
  text-transform: capitalize;
  background-color: #f0f0f0;
  border-radius: 5px;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.4);
  
}

button.key-Enter {
  flex: 2 !important;
}

button.key-Shift {
  flex: 2 !important;
}

button.bk {
  background-color: rgb(86, 86, 86);
}

button.pressed {
  background-color: rgb(57, 54, 202) ;
  box-shadow: inset 8px 10px 9px -4px rgba(0,0,0,0.75);}

